/**
 * col 
 *
 * @prop gap - px (5, 10, 20, 30, 40, 50)
 * @prop pull -  x 
 * @prop push -  x 
 * @prop offset - 
 * @prop span -  12 
 * @prop xs - 
 * @prop s - 
 * @prop m - 
 * @prop l - 
 * @prop xl - 
 * @prop grid - 
 *
 */

import './Col.scss'
import render from './Col.render.js'
import baseMixin from '../../mixin/base'

export default {
  name: 'Col',

  mixins: [baseMixin],

  render,

  props: {
    gap: {
      type: Number,
      default: 0
    },

    pull: {
      type: Number,
      default: 0
    },

    push: {
      type: Number,
      default: 0
    },

    offset: {
      type: Number,
      default: 0
    },

    span: {
      type: Number,
      default: 0
    },

    xs: {
      type: Number,
      default: 0
    },

    s: {
      type: Number,
      default: 0
    },

    m: {
      type: Number,
      default: 0
    },

    l: {
      type: Number,
      default: 0
    },

    xl: {
      type: Number,
      default: 0
    },

    grid: Object
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-col`
    }
  }
}



